export default {
	props: {
		imgList: {
			type: Array,
			default() {
				return []
			}
		},
		urlKey: {
			type: String,
			default() {
				return ''
			}
		},
	},
	watch: {
		imgList: function(val) {
			console.log('val:', val);
		}
	},
	mounted() {
		console.log('imgList', this.imgList);
	},
	data() {
		return {
			currentIndex: 0,
			dontFirstAnimation: true
		}
	},
	methods: {
		swiperChange(e) {
			this.dontFirstAnimation = false
			this.currentIndex = e.detail.current
		},
		clickImg(item) {
			this.$emit('selected', item, this.currentIndex)
		}
	}
}
